'use client';

import Link from 'next/link';
import { GlassCard } from '@/components/ui/GlassCard';
import { NeoFeed } from '@/components/dashboard/NeoFeed';
import { Watchlist } from '@/components/dashboard/Watchlist';
import { AlertCenter } from '@/components/dashboard/AlertCenter';
import { ShieldAlert, Activity } from 'lucide-react';

export default function Home() {
    return (
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-12 w-full relative">

            {/* Background Texture/Glow */}
            <div className="absolute top-0 left-1/2 -translate-x-1/2 w-full h-[500px] bg-gradient-to-b from-nebula-purple/20 via-deep-space/0 to-transparent pointer-events-none z-0" />

            {/* Hero Section */}
            <div className="mb-16 text-center relative z-10">
                <div className="inline-block relative group">
                    <div className="absolute inset-0 bg-star-lord-gold/30 blur-3xl rounded-full opacity-70 group-hover:opacity-100 transition-opacity duration-1000" />
                    <h1 className="relative text-7xl md:text-9xl font-black italic uppercase tracking-tighter mb-4 transform -skew-x-6 text-white drop-shadow-[0_0_25px_rgba(157,78,221,0.8)] border-text">
                        ASTROCITY
                    </h1>
                </div>

                <p className="mt-4 text-xl md:text-2xl font-bold text-cyan-glow tracking-widest uppercase drop-shadow-[0_0_10px_rgba(0,245,212,0.6)] bg-deep-space/40 inline-block px-4 py-2 rounded-lg backdrop-blur-sm border border-cyan-glow/20">
                    Guardians of Planetary Defense
                </p>

                <div className="mt-8 flex justify-center gap-6">
                    <div className="h-1.5 w-16 bg-alert-red skew-x-12 shadow-[0_0_10px_var(--color-alert-red)]" />
                    <div className="h-1.5 w-16 bg-star-lord-gold skew-x-12 shadow-[0_0_10px_var(--color-star-lord-gold)]" />
                    <div className="h-1.5 w-16 bg-cyan-glow skew-x-12 shadow-[0_0_10px_var(--color-cyan-glow)]" />
                    <div className="h-1.5 w-16 bg-nebula-purple skew-x-12 shadow-[0_0_10px_var(--color-nebula-purple)]" />
                </div>
            </div>

            <div className="grid grid-cols-1 lg:grid-cols-3 gap-8 relative z-10">
                {/* Main Visualization Area */}
                <div className="lg:col-span-2 space-y-8">
                    <GlassCard className="min-h-[450px] flex items-center justify-center relative bg-black/60 border-cyan-glow/30 shadow-[0_0_30px_rgba(0,245,212,0.1)] overflow-hidden group">
                        {/* Tech Grid Background */}
                        <div className="absolute inset-0 pointer-events-none opacity-30"
                            style={{ backgroundImage: 'linear-gradient(rgba(0, 180, 216, 0.2) 1px, transparent 1px), linear-gradient(90deg, rgba(0, 180, 216, 0.2) 1px, transparent 1px)', backgroundSize: '50px 50px' }}
                        />

                        <div className="absolute inset-0 flex items-center justify-center pointer-events-none">
                            <div className="w-64 h-64 border border-dashed border-star-lord-gold rounded-full animate-[spin_60s_linear_infinite] opacity-50" />
                            <div className="w-96 h-96 border border-dotted border-cyan-glow rounded-full absolute animate-[spin_40s_linear_infinite_reverse] opacity-50" />
                            <div className="w-32 h-32 bg-cyan-glow/10 rounded-full blur-2xl absolute animate-pulse" />
                        </div>

                        <div className="text-center z-10 bg-black/40 p-8 rounded-2xl backdrop-blur-md border border-white/5 shadow-xl">
                            <p className="text-4xl font-light text-white mb-2 tracking-tighter">ORBITAL VISUALIZER</p>
                            <p className="text-cyan-glow font-bold tracking-widest uppercase text-xs mb-8">3D Real-Time Trajectory Engine</p>
                            <Link href="/visualizer" className="px-8 py-3 bg-white text-black font-black uppercase tracking-wider rounded-sm hover:bg-cyan-glow hover:text-black transition-all shadow-[0_0_20px_rgba(255,255,255,0.2)]">
                                Launch System
                            </Link>
                        </div>
                    </GlassCard>

                    <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                        <Link href="/risk" className="block group">
                            <GlassCard hoverEffect={true} className="h-full border-alert-red/30 bg-alert-red/10 group-hover:bg-alert-red/20 transition-all">
                                <div className="flex justify-between items-start mb-2">
                                    <ShieldAlert className="w-8 h-8 text-alert-red" />
                                    <span className="px-2 py-0.5 bg-alert-red/20 text-alert-red text-[10px] font-black uppercase rounded">Live</span>
                                </div>
                                <h3 className="text-starlight text-sm font-bold uppercase tracking-widest mb-1">Active Alerts</h3>
                                <p className="text-5xl font-black text-white group-hover:text-alert-red transition-colors drop-shadow-[0_0_10px_rgba(255,0,84,0.5)]">2</p>
                                <div className="mt-3 text-xs text-starlight/70 font-medium">
                                    Critical Attention Required
                                </div>
                            </GlassCard>
                        </Link>
                        <Link href="/feed" className="block group">
                            <GlassCard hoverEffect={true} className="h-full border-cyan-glow/30 bg-cyan-glow/10 group-hover:bg-cyan-glow/20 transition-all">
                                <div className="flex justify-between items-start mb-2">
                                    <Activity className="w-8 h-8 text-cyan-glow" />
                                    <span className="px-2 py-0.5 bg-cyan-glow/20 text-cyan-glow text-[10px] font-black uppercase rounded">Live</span>
                                </div>
                                <h3 className="text-starlight text-sm font-bold uppercase tracking-widest mb-1">Objects Tracked</h3>
                                <p className="text-5xl font-black text-white group-hover:text-cyan-glow transition-colors drop-shadow-[0_0_10px_rgba(0,245,212,0.5)]">14k+</p>
                                <div className="mt-3 text-xs text-starlight/70 font-medium">
                                    Scanning Sector 7...
                                </div>
                            </GlassCard>
                        </Link>
                    </div>
                </div>

                {/* Sidebar */}
                <div className="space-y-6">
                    <AlertCenter />
                    <Watchlist />
                    <NeoFeed />

                    <GlassCard className="p-5 bg-gradient-to-br from-nebula-purple/20 to-deep-space border-nebula-purple/40">
                        <h3 className="font-black text-star-lord-gold uppercase tracking-wider mb-3 flex items-center gap-2">
                            <span>//</span> Intel Feed
                        </h3>
                        <div className="text-sm space-y-3 font-mono">
                            <p className="text-starlight/80 border-l-2 border-cyan-glow pl-3">
                                <span className="text-cyan-glow font-bold">@StarLord:</span> Tracking (2024 AB1). Looks fast.
                            </p>
                            <p className="text-starlight/80 border-l-2 border-gamora-green pl-3">
                                <span className="text-gamora-green font-bold">@Gamora:</span> Confirmed safe passage for (2023 FG2).
                            </p>
                        </div>
                    </GlassCard>
                </div>
            </div>
        </div>
    )
}
